import { request } from 'utils';

request.prefix = '/api/categories';

export const getDistricts = () =>
  request({
    url: '/District',
    method: 'GET',
    params: {},
    data: {},
  });
export const getDistrictById = id =>
  request({
    url: `/District/${  id}`,
    method: 'GET',
    params: {},
    data: {},
  });
export const createDistric = data =>
  request({
    url: '/District',
    method: 'POST',
    params: {},
    data,
  });
export const updateDistrict = (id, data) =>
  request({
    url: `/District/${  id}`,
    method: 'PUT',
    params: {},
    data,
  });
export const deleteDistrict = id =>
  request({
    url: `/District/${  id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



